body{
    font-family: "Roboto";
}
a:hover{
    text-decoration: none;
}
h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}
li{
    list-style: none;
}
@media (min-width: 576px) {
    .modal-dialog {
        max-width: 600px;
        height: 100px;
        margin: 1.75rem auto;
    }
}
.section-form__body{
    .group-control{
    display: flex;
    justify-content: center;
    margin-bottom: 23px;
        input[type="text"] {
            border: 1px solid #e5e5e5;
            width: 458px;
            font-size: 14px;
            color: #413d4b;
            padding-top: 17px;
            padding-left: 20px;
            padding-bottom: 16px;
        }
    }
}


textarea{
    border: 1px solid #e5e5e5;
    width: 458px;
    font-size: 14px;
    color: #413d4b;
    padding-top: 17px;
    padding-left: 20px;
    padding-bottom: 16px;
}
textarea:focus{
    outline: none;
}
input[type="text"]:focus {
    outline: none;
}
.header{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 9999;
}
.container {
    @include container();
    @include sm(max-width, rem(510));
    @include md(max-width, rem(690));
    @include lg(max-width, rem(930));
    @include xl(max-width, rem(1110));
}
.header-wrapper{
    &__mobile--navbar{
        &-ul{
            li{
                color: black;
                font-size: 16px;
                text-transform: uppercase;
                margin-bottom: 1rem;
                cursor: pointer;
                &.active{
                    color: #04d4c8;
                }
            }
        }
    }
    display: flex;
    margin-top: 15px;
    margin-bottom: 15px;
    align-items: center;
    &__logo{
        display: flex;
        align-items: center;
        text-decoration: none;
        font-size: 30px;
        flex-grow: 1;
    }
    &__logo img{
        height: 50px;
        width: 44px;
        margin-right: 15px;
    }
    &__logo-title{
        background: linear-gradient(to right, #01ded0, #49a4cc);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    &__navbar-ul{
        display: flex;
        li{
         color: white;
         font-size: 13px;
            text-transform: uppercase;
            margin-right: 24px;
            cursor: pointer;
            &.active{
                color: #04d4c8;
            }
        }
    }
}
.header-bg{
     &__image{
         background-image: url(/img/headerbg.jpg);
         position: relative;
         background-size: cover;
         background-position: center center;
         padding-top: 228px;
         padding-bottom: 208px;
     }
    &__image-overlay{
        background-color: #2d1d54d9;
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
    }
    &__table{
        display: table;
        width: 100%;
        height: 100%;
    }
    &__table-center{
        display: table-cell;
        vertical-align: middle;
    }
    &__table-title{
        font-size: 32px;
        font-weight: bold;
        color: white;
        font-family: 'Playfair Display', serif;
        hr{
            border-bottom: 1px solid #00e0d0;
            width: 50px;
            margin-top: 27px;
            margin-bottom: 31px;
        }
    }
    &__table-description{
        font-size: 14px;
        color: white;
    }
    &__table-description-wrapper{
        margin-bottom: 53px;
    }
}
.header-content{
    max-width: 800px;
}
.main{
    &__class{
        color: red;
        font-weight: bold;
        font-size: 20px;
    }
}
.btn-outline-success {
    color: #03d4c8;
    text-transform: uppercase;
    border-color: #03d4c8;
    font-weight: bold;
}
.section-bg-white {
    &__description-title{
        color: #413d4b;
        font-size: 22px;
        font-weight: bold;
        font-family: 'Playfair Display', serif;
        text-transform: uppercase;
        margin-bottom: 25px;
    }
    &__description-text{
        font-size: 14px;
        color: #817f86;
        margin-bottom: 25px;
    }
    padding-top: 70px;
    padding-bottom: 70px;
}
.section-bg{
    &__video{
        background-image: url(/img/city.jpg);
        position: relative;
        background-size: cover;
        background-position: center center;
        height: 550px;
    }
    &__video-overlay {
        background-color: #2d1d54d9;
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
    }
    &__video-play{
        position: absolute;
        left: 50%;
        cursor: pointer;
        top: 50%;
        transform: translate(-50%, -50%);
        img{
            padding-bottom: 20px;
            margin: 0 auto;
            display: flex;
        }
    }
    &__video-play-description{
        color: white;
        font-size: 16px;
        text-transform: uppercase;
    }

}
.true-detective-iframe{
    width: 100%;
}
.section-expertise{
    &__header{
        margin-bottom: 48px;
    }
    hr{
        width: 50px;
        border: 1px solid #c6c4c9;
    }
    &__title{
        color: #413d4b;
        font-size: 22px;
        font-weight: bold;
        font-family: 'Playfair Display', serif;
        text-transform: uppercase;
        text-align: center;
    }
    &__description{
        font-size: 14px;
        color: #817f86;
        margin-bottom: 18px;
        text-align: center;
    }
    &__body{
        .col-md-4:nth-child(3n-1){
            border-left: 1px solid #ebebeb;
            border-right:1px solid #ebebeb;
        }
        .col-md-4:nth-child(-n+3){
            border-bottom: 1px solid #ebebeb;
        }
    }
    &__wrapper{
        justify-content: center;
        display: flex;
        padding: 33px;
        &-item img{
            display: flex;
            margin: 0 auto;
            margin-bottom: 35px;
        }

    }
    &__item-title{
        text-align: center;
        font-weight: bold;
        margin-bottom: 25px;
        font-size: 14px;
    }
    &__item-description{
        font-size: 14px;
        color: #817f86;
        text-align: center;
    }
    padding-top: 71px;
    padding-bottom: 68px;
}
.section-team{
    &__body{
        margin-bottom: 71px;
    }
    &__image{
        background-image: url(/img/teambg.jpg);
        position: relative;
        background-size: cover;
        padding-bottom: 69px;
        padding-top: 71px;
        background-position: center center;
    }
    &__image-overlay{
        background-color: #2d1d54d9;
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
    }
    &__header hr{
        width: 50px;
        border-bottom: 1px solid #655b80;
        margin-bottom: 47px;
    }
    &__header-title{
        font-family: 'Playfair Display', serif;
        font-weight: bold;
        text-transform: uppercase;
        color: white;
        font-size: 22px;
    }
    &__header-description{
        font-size: 14px;
        color: white;
    }
    &__body-card{
        cursor: pointer;
    }
    &__body-card-image{
        width: 100%;
        height: 229px;
        background-color: white;
    }
    &__body-card-title{
        color: #f4f3f6;
        text-transform: uppercase;
        font-size: 14px;
        font-weight: bold;
        text-align: center;
    }
    &__body-card-footer{
        padding: 22px;
        border: 1px solid #77768a;
        text-align: center;
    }
    &__body-card-description{
        color: #77768a;
        font-size: 13px;
    }
    &__footer{
        &--description{
            color:#f3f3f3;
            margin-bottom: 30px;
        }
    }
}
.section-works{
    &__header{
        padding-bottom: 71px;
        padding-top: 71px;
        display: flex;
        align-items: center;
        &--title{
            font-family: 'Playfair Display', serif;
            font-size: 22px;
            font-weight: bold;
            color:#413d4b;
            flex-grow: 1;
            text-transform: uppercase;
        }
        &--viewall{
            color:#413d4b;
            font-size: 14px;
            .fa-chevron-right{
                font-size: 12px;
                color: #413d4b;
            }
        }

    }
    &__body{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        &--item{
            max-width: 16.666667%;
            flex: 0 0 16.666667%;
            img{
                width: 100%;
            }
        }
    }
    &__footer{
        padding-top: 70px;
        padding-bottom: 70px;
        display: flex;
        justify-content: center;
    }
}
.section-reviews{
    background-image: url(/img/reviewsbg.jpg);
    position: relative;
    padding-top: 69px;
    background-size: cover;
    background-position: center center;
    &__header{
        font-size: 100px;
        position: relative;
        color: white;
        line-height: 1;
        text-align: center;
        font-family: 'Playfair Display', serif;
    }
    &__body{
        &-item{
            display: none;
            &.active{
                display: block;
            }
        }
        &--review{
        font-size: 16px;
        font-family: 'Playfair Display', serif;
        color: white;
        line-height: 1.9;
            padding-bottom: 55px;
        }
        &--author{
            font-size: 14px;
            color: white;
            text-transform: uppercase;
            font-weight: bold;
            margin-bottom: 13px;
            letter-spacing: 1px;
        }
        &--works{
            font-size: 13px;
            color: #bebbc9;
        }
    }
    &__footer{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 45px;
        margin-bottom: 81px;
        &--face{
            img{
                width: 45px;
            }
            margin-right: 14px;
            cursor: pointer;
            &.active{
                img{
                    width: 70px;
                    height: 70px;
                }
            }
        }
    }
}
.section-form{
    padding-top: 71px;
    padding-bottom: 70px;
    &__header{
        color: #413d4b;
        font-weight: bold;
        font-size: 22px;
        text-align: center;
        font-family: 'Playfair Display', serif;
        margin-bottom: 35px;
    }
    &__body{
        button{
            display: flex;
            margin: 0 auto;
        }
    }
}
.footer{
    background-image: url(/img/footerbg.jpg);
    position: relative;
    background-size: cover;
    background-position: center center;
    padding-top: 70px;
    &-wrapper{
        &--footer{
            margin-top: 70px;
            padding-bottom: 72px;
            &__copy{
                color: white;
                font-size: 14px;
            }
            &__links{
                ul{
                    display: flex;
                    li{
                        color: white;
                        text-transform: uppercase;
                        font-size: 14px;
                        margin-right: 34px;
                    }
                }
            }
        }
        &__logo{
            display: flex;
            align-items: center;
            text-decoration: none;
            font-size: 30px;
            height: 77px;
            img{
                margin-right: 14px;
            }
            span{
                background: linear-gradient(to right, #01ded0, #49a4cc);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
        }
        &__description{
            color: #b8b3c4;
            font-size: 14px;
        }
        &__studio{
            &--header{
                color: white;
                font-size: 15px;
                font-weight: bold;
                display: flex;
                align-items: center;
                letter-spacing: 1px;
                height: 77px;
                text-transform: uppercase;
            }
            &--social{
                display: flex;
                margin-top: 27px;
                img{
                    margin-right: 8px;
                }
            }
            &--body{
                font-size: 14px;
                color: #b8b3c4;
                &--item{
                    margin-bottom: 24px;
                }
            }
        }
    }
}
.footer--wrapper__studio--form--input{
    background: none;
    color: white;
    padding-top: 17px;
    width: 209px;
    margin-right: 10px;
    padding-left: 17px;
    height: 45px;
    padding-bottom: 16px;
    border: 1px solid #9e97b0;
    font-size: 14px;
}
.footer--wrapper__studio--form--input::-webkit-input-placeholder{
    color: #b8b3c4;
}
.footer--wrapper__studio--form--button{
    width: 70px;
    height: 45px;
    background: #9e97b0;
    border: none;
}
.footer--wrapper__studio--form{
    .group-control{
        display: flex;
    }
}
@media (max-width: 991px){
    .header-wrapper__navbar{
        display: none;

    }
}
@media (min-width: 992px){
    .fa-bars{
        display: none;
    }
}
.fa-bars{
    background: linear-gradient(to right, #01ded0, #49a4cc);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 36px;
}
.mobile-menu{
    .modal-dialog{
        margin: 2rem;
        margin-top: 4.5rem;
    }
}